import React from "react";
import { connect } from "react-redux";
import "./ShopCategoryItem.css";
import { selectCollectionCategory } from "../../Redux/Shop/ShopSelector";
import ItemComponent from "../ItemComponent/ItemComponent";

const ShopCategoryItem = ({ collectionCategory, match }) => {
  console.log(collectionCategory);
  const { title, items, category } = collectionCategory;
  return (
    <div className="shopCategory">
      <span className="title"> {title} </span>
      <div className="shopCategoryItem">
        {items.map((item) => (
          <ItemComponent key={item.id} item={item} category={category} />
        ))}
      </div>
      {/* <Route exact path={`${match.path}/product`} component={ProductPage} /> */}
    </div>
  );
};

const mapStateToProps = (state, ownProps) => ({
  collectionCategory: selectCollectionCategory(
    ownProps.match.params.collectionId
  )(state),
});

export default connect(mapStateToProps, null)(ShopCategoryItem);
